<template>
  <div class="wrapper">
    <div class="text">
      <i class="fa-solid fa-quote-left"></i>
      {{ text }}
      <i class="fa-solid fa-quote-right"></i>
    </div>
    <div class="author">— {{ author }}</div>
    <button @click="next_quote" class="next">Next</button>
  </div>
</template>

<script>
export default {
  name: "QuotePage",
  props: ['id'],
  data() {
    console.log(this.id);
    let quote = this.$store.state.quotes.find(q => q.id === +this.id);
    if (!quote) {
      this.$router.push({name: "quote", params: {id: 1}});
      return;
    }

    return {
      text: quote.text,
      author: quote.author
    }
  },
  methods: {
    next_quote() {
      let n = this.$store.state.quotes.length;

      let next = +this.id;
      while (next === +this.id)
        next = Math.floor(Math.random() * n);

      this.$router.push({name: "quote", params: {id: next}});
    }
  }
}
</script>

<style lang="scss" scoped>
.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  width: 40vw;
  min-width: 400px;
  background-color: white;
  padding: 50px 25px 35px;
  border-radius: 10px;

  .text {
    width: 100%;
    text-align: justify;
    font-size: 35px;

    i {
      font-size: 25px;
    }
  }

  .author {
    width: 100%;
    font-style: italic;
    font-size: 20px;
    text-align: right;
    padding-right: 25px;
  }

  button {
    width: 100px;
    font-size: 20px;
    padding: 5px;
    margin-top: 15px;
    color: white;
    font-weight: bold;
    letter-spacing: 2px;
    border-radius: 5px;
    transition: all 0.5s;
    background: #009FFF;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #ec2F4B, #009FFF);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #ec2F4B, #009FFF); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    background-size: 400% 100%;
    text-transform: uppercase;
    &:hover {
      background-position: 100%;
    }
  }
}
</style>